import mongoose from "mongoose"
const Schema = mongoose.Schema
/**
 * @desc 
 */
const newsSchema = new Schema({
  id: String,
  title: String,  //
  abstract: String,//
  article: String, //
  cover_img_src: {type: String, default: "../../assets/img/7.jpg"}, //
  content_img_src: Array, //
  url: String, //
  date: String, //
  view_count: String, //
})

/**
 * @desc
 */
const NewsModel = mongoose.model('news', newsSchema, 'MaoYanNews')

export default NewsModel



