/**
 * @desc 2018
 * @param currentPage: 
 * @return 30
 * */

router.get('/getHotSortedMovies', async (req, res, next) => {
  console.log('#################');
  let page_length = 30;   // 30
  let page = req.query.currentPage
  console.log(page);
  let count = (page - 1) * page_length
  let MoviesArr = await MovieModel.find({online_time: {$regex: '2018'}}).exec();
  let findMovies = [];
  let YiArr = []    // 
  let WanArr = []   // 
  for (let item of MoviesArr) {
    let movie_price = item.movie_total_price
    if (movie_price.indexOf('') != -1) {
      let YiStr = item.movie_total_price.split('')[0]
      let YiNum = Number(YiStr)
      YiArr.push(YiNum)
    } else if (movie_price.indexOf('') != -1) {
      let WanStr = item.movie_total_price.split('')[0]
      let WanNum = Number(WanStr)
      WanArr.push(WanNum)
    }
  }
  function compare(value1, value2) {
    return value2 - value1
  }
  YiArr.sort(compare)
  WanArr.sort(compare)
  let storeMovie = []
  for (var i = 0; i < (YiArr.length + WanArr.length); i++) {
    if (i < YiArr.length) {
      let price = String(YiArr[i]) + ''
      let Arr = await MovieModel.find({movie_total_price: price}).exec();
      for (let item of Arr) {
        let ob = JSON.parse(JSON.stringify(item));
        storeMovie.push(ob)
      }
    } else {
      let price = String(WanArr[i - YiArr.length]) + ''
      let Arr = await MovieModel.find({movie_total_price: price}).exec();
      for (let item of Arr) {
        let ob = JSON.parse(JSON.stringify(item));
        storeMovie.push(ob)
      }
    }
  }
  for (var i = count; i < count + page_length; i++) {
    findMovies.push(storeMovie[i])
  }
  return _dbSuccess(res, '', findMovies)
});

