/**
 * @desc 
 * @param   online_time: 
                country: 
                   type: 
            currentPage: 
 * @return 30
 * */

// 
 function searchByYear(inputArr, year) {
  let outputArr = []
  let startYear = 1950
  let endYear = 2025
  if (year == '2018') {
    startYear = 2018
    endYear = 2018
  } else if (year == '2011-2017') {
    startYear = 2011
    endYear = 2017
    console.log(startYear)
  } else if (year == '2000-2010') {
    startYear = 2000
    endYear = 2010
  } else if (year == '90') {
    startYear = 1990
    endYear = 1999
  } else if (year == '80') {
    startYear = 1980
    endYear = 1989
  } else if (year == '70') {
    startYear = 1970
    endYear = 1979
  } else if (year == '') {
    startYear = 1950
    endYear = 1969
  }
  for (var i = 0; i < inputArr.length; i++) {
    for (var j = startYear; j <= endYear; j++) {
      if (inputArr[i].online_time.indexOf(String(j)) != -1) {
        outputArr.push(inputArr[i])
        //console.log('Yes')
      }
    }
    console.log(inputArr)
  }
  return outputArr
 }

// 
 function searchByType(inputArr, type) {
  let outputArr = []
  for(var i = 0; i < inputArr.length; i++) {
    if (inputArr[i].movie_type.indexOf(type) != -1) {
      outputArr.push(inputArr[i])
    }
  }
  return outputArr
 }

