import crypto from 'crypto'
import http from 'http'
import queryString from 'querystring'
import {SMS} from '../config'
import { Router } from 'express'
const router = Router()

//config
const smsapi= SMS.smsapi
const user = SMS.user
const password = SMS.password
var md5 = crypto.createHash('md5')

function statusStr(result){
  switch(result)
  {
    case '0':
      console.log('')
      break
    case '-1':
      console.log('')
      break
    case '-2':
      console.log(',curlfsocket')
      break
    case '30':
      console.log('')
      break
    case '40':
      console.log('')
      break
    case '41':
      console.log('')
      break
    case '42':
      console.log('')
      break
    case '43':
      console.log('IP')
      break
    case '50':
      console.log('')
      break
  }
}

router.post('/sms', async(req, res, next) => {
    var smscontent = "" + req.body.code;
    var phone = req.body.cellphone;
    var pass = md5.update(password).digest('hex');
    var data={
      'u':user,
      'p':pass,
      'm':phone,
      'c':smscontent
    }
    var content=queryString.stringify(data);
    var options={
      hostname : smsapi,
      path:'/sms?'+content,
      method:'GET'
    }
  //
    var request = http.request(options,function(response){
      response.setEncoding('utf-8');
      response.on('data',function(result){
      statusStr(result)
      });
      response.on('end',function(){
      });
    });
    request.on('error',function(err){
      console.error(err);
    });
    request.end();
    return res.json({code: req.body.code})
  }
)

export default router



