import mongoose from 'mongoose'
const Schema = mongoose.Schema;

/**
 * @desc 
 */

const movieSchema = new Schema({
  id: String,
  movie_name: String,   //
  movie_ename: String,  //
  country: String,      //
  movie_type: String,   //
  movie_time: String,   //
  online_time: String,  //
  movie_star: String,   //
  movie_total_price: String,   //
  img: String,          //
  director: String,     //
  director_src: String, //
  actor: Array,        //
  actor_src: Array,    //
  introduction: String, //
});

/**
 * @desc
 */
const MovieModel = mongoose.model('movie', movieSchema, 'movie');

export default MovieModel



