import mongoose from 'mongoose'
const Schema = mongoose.Schema

/**
 * @desc 
 */
const videoSchema = new Schema({
  id: String,
  title: String,  //
  cover_img_src: String, //
  url: String, //
  view_count: String, //
})

/**
 * @desc
 */
const VideoModel = mongoose.model('videos', videoSchema, 'MaoYanVideos');

export default VideoModel



