import mongoose from "mongoose"
const Schema = mongoose.Schema
/**
 * @desc 
 */
const cinemaSchema = new Schema({
  id: String,
  cinema_name: String,  //
  district: String,     //
  online_moive: Array,  //
  address: String,      //
  telephone: String,     //
  img_url: String,      //
})

/**
 * @desc
 */
const CinemaModel = mongoose.model('cinema', cinemaSchema, 'cinema')

export default CinemaModel



